/* eslint-disable */
<template>
    <div>
        <div class="row">
            <div class="col-md-8 col-md-push-2">
                    <form v-on:submit.prevent>
                        <div class="form-group">
                            <input type="text" v-on:keyup.enter="add()" v-model="input" class="form-control col-md-6" id="todoitem" placeholder="Add Todo Item Here" />
                            <button tabindex="0" type="button" v-on:click="add()" class="btn btn-default">+</button>
                        </div>
                    </form>
            </div>
        </div>
        <div class="row">
            <div class="col-md-12">
                <h2>List</h2>
                <ul class="list-group">
                    <li v-for="(todo, index) in todos" :key="index + 1" class="list-group-item">
                        <button type="button" v-on:click="completeItem(todo)" id="btn-complete" class="btn btn-complete">(&nbsp;&nbsp;&nbsp;&nbsp;)</button> {{ index }}: {{ todo }} <button type="button" v-on:click="deleteItem(todo)" class="btn btn-delete">x</button>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>  
 
<script>
const STORAGE_KEY = "todo-storage";
export default {
  name: "Todo",
  data() {
    return {
      todos: [],
      input: ""
    };
  },
  created() {
    this.todos = JSON.parse(localStorage.getItem(STORAGE_KEY) || "[]");
  },
  methods: {
    add() {
      if (this.input === '') {
        alert('Please enter a to-do item');
      } else {
      this.todos.push(this.input);
      this.input = "";
      localStorage.setItem(STORAGE_KEY, JSON.stringify(this.todos));
      }
    },
    deleteItem(todo) {
      this.todos.splice(this.todos.indexOf(todo), 1);
      localStorage.setItem(STORAGE_KEY, JSON.stringify(this.todos));
    },
    completeItem(index) {
      var completed = false;
      if (!completed) {
      document.getElementById('btn-complete').innerHTML = '( x )';
      var completed = true;
      } else if (completed) {
        document.getElementById('btn-complete').innerHTML = '(&nbsp;&nbsp;&nbsp;&nbsp;)'
      }
    }
  }
};
</script>  


