<template>
  <div id="app">
    <Todo/>
  </div>
</template>

<script>
import Todo from "./components/index";

export default {
  name: "App",
  components: {
    Todo
  }
};
</script>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
.list-group {
  width: 40vw;
  margin: auto;
}

.form-group.btn {
  position: absolute;
  top: 3px;
  right: 3px;
  font-size: 5rem;
  margin: 0;
  padding: 0px 10px 7px;
  line-height: 0.5;
}

.btn {
  transition: .5s;
}

.btn-default {
  position: absolute;
}

.btn-delete {
  position: absolute;
  right: 0;
  bottom: 0;
  margin: 0;
  height: 100%;
  font-size: 1.5rem;
  line-height: 2rem;
  border-radius: 0;
}

.btn-delete:hover {
    background: #ff8d8d;
    /* box-shadow: #ff8180 0 -3px 1px inset; */
    transition: .5s;
}

.btn-complete {
  position: absolute;
  left: 0;
  bottom: 0;
  margin: 0;
  height: 100%;
  border-radius: 0;
}
.btn-complete:hover {
    background: #7ef1b7;
    /* box-shadow: #ff8180 0 -3px 1px inset; */
    transition: .5s;
}



</style>



